import React, { Component } from 'react';
import { TweenMax } from 'gsap';

import '../css/contact.scss'

class Contact extends Component {

  constructor(props){
    super(props);
    this.state = {
      allIn: false,
      cheked: false,
      isTarea: false,
      isProcess: false
    };

    this.btnNext = document.getElementsByClassName('btn_next');
  }

  componentDidMount(){
    // console.log('didMount');
  }

  handleChange(e){

    if(!this.state.isProcess){

      // console.log('입력중', e.target.value);
      const nextArea = e.target.parentNode.nextElementSibling;

      // 다음셀에 ipt_wrap이 계속 있을때
      if(nextArea.classList.contains('ipt_wrap')){

        if(!e.target.value == ''){
          // console.log('입력값 있음');
          // 입력값이 있으면 다음셀 보여주기
          nextArea.classList.add('show');
    
        } else {
          // console.log('입력값 없음');
          // 위로 거슬러 올라가서 지울때 
          // 다음셀에 입력된게 없다면 가리기
          const nextVal = nextArea.querySelector('.ipt').value;
          if(nextVal == ''){
            nextArea.classList.remove('show');
          }
        }
      }
    }




    const iptEl = document.getElementsByClassName('ipt_wrap');
    const iptElLeng = iptEl.length;
    let valLeng = iptElLeng;

    // 입력값이 들어왔는지 check
    Array.prototype.forEach.call(iptEl, (el, i) => {
      // console.log(el.querySelector('.ipt').value, i);
      if(el.querySelector('.ipt').value == ''){
        valLeng = valLeng - 1;
      }

      // resize input
      // let iptVal = el.querySelector('.ipt').value;
      // let valLeng = iptVal.length;
      // console.log(i + ':' + valLeng );
      // el.querySelector('.ipt').style.width = valLeng*int + 50 + 'px';
    });

    // fake Input
    if(e.target.classList.contains('autosize')){
      const fakeIpt = document.querySelector('.fake');
      fakeIpt.textContent = e.target.value;

      let fakeWidth = fakeIpt.clientWidth;
      e.target.style.width = fakeWidth + 'px';

      // console.log(fakeWidth);
    };


    // console.log(valLeng);
    // Constructor 접근을 위해 bind(this) 필요

    if(valLeng == iptElLeng){
      this.setState({
        allIn : true
      });
      // console.log('전체 값이 입력된 상태');
    } else {
      this.setState({
        allIn : false
      });
    }

    // 의뢰내용 입력시 Next 버튼 표출하기
    if(e.target.tagName == 'TEXTAREA'){
      // console.log('의뢰내용 입력중');
      this.setState({
        isTarea: true,
        isProcess: true
      });

      console.log(e.target.scrollHeight);
      e.target.style.height = '100px';
      e.target.style.height = (10 + e.target.scrollHeight) +'px';
    }

    // tel number hyphen
    if(e.target.getAttribute('id') == 'telNum'){
      // let hp = e.target.value;
      // let newHp = hp.replace(/(^02.{0}|^01.{1}|[0-9]{3})([0-9]+)([0-9]{4})/,"$1-$2-$3");
      // console.log(newHp);
    }

  }

  handleFocus(e){
    const entInput = e.target.parentNode;
    entInput.classList.add('focus');
    // console.log('focus');

    // fake Input
    if(e.target.classList.contains('autosize')){
      const fakeIpt = document.querySelector('.fake');
      fakeIpt.classList.add('focus');
      fakeIpt.textContent = e.target.value;
  
      let fakeWidth = fakeIpt.clientWidth;
      e.target.style.width = fakeWidth + 'px';
    };


  }

  handleBlur(e){
    const entInput = e.target.parentNode;
    
    // fake Input
    if(e.target.classList.contains('autosize')){
      const fakeIpt = document.querySelector('.fake');
      fakeIpt.classList.remove('focus');
      fakeIpt.textContent = e.target.value;

      let fakeWidth = fakeIpt.clientWidth;
      e.target.style.width = fakeWidth + 'px';
    };

    
    if(e.target.tagName == 'TEXTAREA'){
      e.target.style.height = '100px';
      e.target.style.height = (10 + e.target.scrollHeight)/1.36 +'px';
    }


    // console.log('떠났따', e.target.value);
    entInput.classList.remove('focus');

    
    if(!e.target.value == ''){
      // 떠날때 값이 있으면
      // 입력한 셀 완료처리
      entInput.classList.add('entered');
    } else {
      // 떠날때 값이 없으면 (입력하지 않고 위로 올라갈경우)
      // 입력하던 셀 가리기

      if(!this.state.isProcess){
        entInput.classList.remove('show');
        entInput.classList.remove('entered');
      }

      /*
      1. 의뢰내용 입력시 Next 버튼 표출 O
         값이 모두 입력되어야 Next 버튼 활성화 O
         프로세스 진행완료 체크 (isProcess) O

      2. 순차적인 수정이 아닐때 가운데 뻥뚫려 버리는 이슈  O
         단계별 입력을 위한 UX이니 전체 프로세스를 거쳤는지 체크 O
         최초 프로세스에서만 실행하기 O
      */
    }
  }

  handleCheck(e) {
    if(e.target.checked){
      this.setState({
        checked: true
      });
    } else {
      this.setState({
        checked: false
      });
    }
  }


  render() {
    return (
      <React.Fragment>

        <div className="contact">

          <span className="fake"></span>

          <div className="ipt_wrap default show">
            회사명은
            <input type="text" id="txt" className="ipt autosize" onChange={this.handleChange.bind(this)} onFocus={this.handleFocus.bind(this)} onBlur={this.handleBlur.bind(this)} />
            입니다.
          </div>

          <div className="ipt_wrap">
            이메일 주소는
            <input type="text" className="ipt autosize" onChange={this.handleChange.bind(this)} onFocus={this.handleFocus.bind(this)} onBlur={this.handleBlur.bind(this)} />
            입니다.
          </div>

          <div className="ipt_wrap">
            연락처는
            <input type="text" id="telNum" className="ipt autosize" onChange={this.handleChange.bind(this)} onFocus={this.handleFocus.bind(this)} onBlur={this.handleBlur.bind(this)} />
            입니다.
          </div>

          <div className="ipt_wrap">
            의뢰내용
            <textarea name="" id="" cols="30" rows="10"
               className="ipt" onChange={this.handleChange.bind(this)} onFocus={this.handleFocus.bind(this)} onBlur={this.handleBlur.bind(this)}
            ></textarea>
          </div>

          <div className="ipt_wrap">
            <div className="checkwrap">
              <input type="checkbox" id="chk1" className="ipt" onChange={this.handleCheck.bind(this)} />
              <label htmlFor="chk1">개인정보 수집·이용에 동의</label>
            </div>

                        
            {/* <span className="checkwrap">
                <input type="checkbox" id="ck1" className="ipt" onChange={this.handleCheck.bind(this)} checked="" />
                <label htmlFor="ck1">개인정보 수집·이용에 동의</label>
            </span> */}

          </div>

          {this.state.isTarea ? 
          <button className={`${this.state.allIn && this.state.checked ? 'color' : ''} btn_next`}>Next</button>
          : null
          }

        </div>
        
      </React.Fragment>
    );
  }
}

export default Contact;
